
  import {setTitle} from '@/common/js/common';
  import XInput from 'vux/src/components/x-input/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
;

  export default{
    data(){
      return{
        isLoading:false,
        pay_price:0,
        radio003: [{
          icon: 'data:image/png;base64,',
          key: '1',
          value: ''
        }, {
          icon: 'data:image/png;base64,',
          key: '2',
          value: ''
        }, {
          icon: 'data:image/png;base64,',
          key: '3',
          value: ''
        },{
          icon:'data:image/png;base64,',
          key:'4',
          value:''
        }],
        radio004: [{
          icon: 'data:image/png;base64,',
          key: '1',
          value: ''
        }, {
          icon: 'data:image/png;base64,',
          key: '2',
          value: ''
        }, {
          icon: 'data:image/png;base64,',
          key: '3',
          value: ''
        }],
        choose:'3',
        order_id:"",
        dialog_title:"",
        content:"",
        confirm_show:false,
        type:0,
        pay:0,
        pay_page:false,
        bill_msg:"",
        address:"",
        choose_value:"",
        send_type:["","",""],
        show3:false,
        password:"",
        confirm_type:false,
        password_mark:false,
        mask:0,
        show_toast:false,
        toast_text:"",
        pay_num:0,
        pay_type:0,
        pay_name:"" //
      }
    },
    directives: {
      TransferDom
    },
    components:{
      Loading,XInput,Group,Radio,XButton,Alert,XSwitch,XTextarea,Checker,CheckerItem,Confirm,Toast
    },
    mounted(){
        /*   1   2    3   4*/
        this.type= parseInt(this.$route.query.type);
        this.pay_type= this.$route.query.pay_type?"("+this.$route.query.pay_type+")":"";
        this.pay_num= this.$route.query.pay_num;
        this.pay_name=this.switch_name(this.type);
        this.pay= parseFloat(this.$route.query.pay);
        this.order_id = this.$route.query.order_id?this.$route.query.order_id:"";
      document.getElementById("index_loading").style.display="none";
    },
    methods:{
      change(value){
        console.log('change:', value);
        this.choose=value;
      },
      o_validate(){
        if(this.bill_msg){
          this.dialog_title="";
          this.content="";
          this.confirm_show=true;
          return false;
        }
        if(this.address){
          this.dialog_title="";
          this.content="";
          this.confirm_show=true;
          return false;
        }
      },
      go_pay(){
          /*if(this.pay_page){
            if(!this.o_validate){
                return;
            }
            const this_item = {
              ticketinfo:this.bill_msg,
              ticketmethod:this.bill_msg,
              address:this.bill_msg,
              ordernum:this.order_id
            };
            this.$http.post(service_url+"/o2o/assetsv2/ticket/add_bill",this_item).then((data)=>{
              if(data.body.status===0){

              }else{
                this.dialog_title="";
                this.content=data.body.error_reason;
                this.confirm_show=true;
              }
            })
          }*/
          let choose = parseInt(this.choose);
          if(this.choose){
              const items = {
                emoney:this.pay_price
              };
              switch (choose){
                case 1:
                    if(this.type===1){
                        if(this.pay_price&&this.pay_price>0){
                          if(!this._validate()){
                              return;
                          }
                          this.$router.push({path:"my_bankCardList",query:{pay:this.pay_price,type:this.type,show:1}});
                        }else{
                          this.dialog_title="";
                          this.content="";
                          this.confirm_show=true;
                        }

                    }else if(this.type===2||this.type===3||this.type===4){
                      this.$router.push({path:"my_bankCardList",query:{type:this.type,order_id:this.order_id,show:1}});
                    }
                  break;
                case 2:
                    if(this.type===1){
                      if(this.pay_price&&this.pay_price>0){
                        if(!this._validate()){
                          return;
                        }
                        this.$router.push({path:"Alipay",query:{pay:this.pay_price,type:this.type}});
                      }else{
                        this.dialog_title="";
                        this.content="";
                        this.confirm_show=true;
                      }
                    }else if(this.type===2||this.type===3||this.type===4){
                      this.$router.push({path:"Alipay",query:{type:this.type,order_id:this.order_id}});
                    }
                  break;
                case 3:
                  if(this.type===1){
                    if(this.pay_price&&this.pay_price>0){
                      if(!this._validate()){
                        return;
                      }
                      this.$http.post(service_url+"/o2o/assetsv2/wx/get_orderid.do",items).then((data)=>{
                        if(data.body.status===0) {
                          const item = {
                            orderId:data.body.data.order_id
                          };
                          this.$http.post(service_url+"/o2o-assetsv2-webAndroid/wx/weixin/pay",item).then((odata)=>{
                            function onBridgeReady(){
                              WeixinJSBridge.invoke(
                                'getBrandWCPayRequest', {
                                  "appId":odata.body.data.appId,     //
                                  "timeStamp":odata.body.data.timeStamp,         //1970
                                  "nonceStr":odata.body.data.nonceStr, //
                                  "package":odata.body.data.package,
                                  "signType":odata.body.data.signType,         //
                                  "paySign":odata.body.data.paySign, //
                                  "packageValue":odata.body.data.packageValue, //
                                  "sendUrl":odata.body.data.sendUrl //
                                },
                                function(res){
                                    /**/
//                                  alert(JSON.stringify(res));
//                                  alert(res.err_msg);
                                  if(res.err_msg == "get_brand_wcpay_request:ok" ) {

                                  }     // ,res.err_msg    ok
                                  else{
                                    this.dialog_title="";
                                    this.content="";
                                    this.confirm_show=true;
                                  }
                                }
                              );
                            }
                            if (typeof WeixinJSBridge == "undefined"){
                              console.log("un");
                              console.log(document.addEventListener);
                              if( document.addEventListener ){
                                document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                              }else if (document.attachEvent){
                                document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                                document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                              }
                            }else{
                              onBridgeReady();
                            }
                          });
                        }
                      });
                    }else{
                      this.dialog_title="";
                      this.content="";
                      this.confirm_show=true;
                    }
                  }else if(this.type===2||this.type===3||this.type===4){
                    const item = {
                      orderId:this.order_id
                    };
                    this.$http.post(service_url+"/o2o-assetsv2-webAndroid/wx/weixin/pay",item).then((data)=>{
                      function onBridgeReady(){
                        WeixinJSBridge.invoke(
                          'getBrandWCPayRequest', {
                            "appId":data.body.data.appId,     //
                            "timeStamp":data.body.data.timeStamp,         //1970
                            "nonceStr":data.body.data.nonceStr, //
                            "package":data.body.data.package,
                            "signType":data.body.data.signType,         //
                            "paySign":data.body.data.paySign //
                          },
                          function(res){
//                            alert(res.err_msg);
                            if(res.err_msg == "get_brand_wcpay_request:ok" ) {

                            }     // ,res.err_msg    ok
                            else{
                              this.dialog_title="";
                              this.content="";
                              this.confirm_show=true;
                            }
                          }
                        );
                      }
                      if (typeof WeixinJSBridge == "undefined"){
                        console.log("un");
                        console.log(document.addEventListener);
                        if( document.addEventListener ){
                          document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                        }else if (document.attachEvent){
                          document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
                          document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                        }
                      }else{
                        onBridgeReady();
                      }
                    });
                  }
                  break;
                case 4:
                    this.show3=true;
              }
            }else{
            this.dialog_title="";
            this.content="";
            this.confirm_show=true;
            }
      },
      _validate(){
        if(this.pay_price>50000-this.pay){
          this.dialog_title="";
          this.content="5W";
          this.confirm_show=true;
          return false;
        }
        return true;
      },
      itemclick(val){
        this.choose_value = val;
      },
      onConfirm(){
        let reg = /^\d{6}$/;
        if(!reg.test(this.password.trim())){
            this.password_mark=true;
          /*this.dialog_title="";
          this.content="6";
          this.confirm_show=true;*/
          return;
        }
        this.isLoading=true;
        if(service_url){
            const items = {
              orderId:this.order_id,
              password:this.password
            };
            this.$http.post(service_url+"/o2o/assetsv2/wx/kenai/pay",items).then((data)=>{
                this.isLoading=false;
                if(data.body.status===0){
                  this.show3=false;
                  this.show_toast=true;
                  this.toast_text="";
                  setTimeout(this.go_back,1000);
                }else{
                  this.dialog_title="";
                  this.content=data.body.error_reason;
                  this.show3=false;
                  this.mask=1;
                  this.confirm_show=true;
                  return false;
                }
            })
        }else{
         /* this.dialog_title="";
          this.content="";
          this.show3=false;
          this.mask=1;
          this.confirm_show=true;
          return false;*/
          this.isLoading=false;
          this.show3=false;
          this.show_toast=true;
          this.toast_text="";
          setTimeout(this.go_back,1000);
        }
      },
      onCancel(){
        this.password="";
        this.password_mark=false;
      },
      onHide(){
        if(this.mask===1){
            this.show3=true;
            this.mask=0;
        }
      },
      go_back(){
          this.$router.go(-1);
      },
      switch_name(type){
        switch(type){
          case 2:
              return "";
          break;
          case 3:
              return "";
              break;
          case 4:
              return "";
              break;
        }
      }
    }
  }



